<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1 class="display-3 heading">Detail Dispatch</h1>
      </div>
      <div class="card col-md-3" v-for="(req, index) in reqs" :key="index">
        <div class="card-body">
          <v-layout @click="delete_request(req.id)" justify-end class="delete mb-2">
            <span class="group">
              <v-icon>delete</v-icon>
            </span>
          </v-layout>
          <h5 class="card-title">Stock #: {{req.stock_number}}</h5>
          <p class="card-text">Sales Associate: {{req.sales_associate}}</p>
          <p class="card-text">Customer Name: {{req.customer_name}}</p>
          <p class="card-text">Type of Vehicle: {{req.type_of_vehicle}}</p>
          <p class="card-text">Year: {{req.year}}</p>
          <p class="card-text">Model: {{req.model}}</p>
          <p class="card-text">License Plate Bracket: {{req.license_plate}}</p>
          <p class="card-text">Detail Associate: {{req.carwash_associate}}</p>
          <hr />
          <!-- input fields -->
          <label v-if="req.carwash_state" for="status_carwash">Status</label>
          <select
            id="status_carwash"
            v-if="req.carwash_state"
            class="form-control form-control-lg"
            v-model="req.status_carwash"
            required
          >
            <option v-for="(approval, index) in approvals" :key="index">{{ approval }}</option>
          </select>
          <hr v-if="req.carwash_state" />
          <label v-if="req.carwash_state" for="carwash_associate">Car Wash Associate</label>
          <select
            id="carwash_associate"
            v-if="req.carwash_state"
            class="form-control form-control-lg"
            v-model="req.carwash_associate"
            required
          >
            <option v-for="(man, index) in managers" :key="index">{{ man }}</option>
          </select>
          <div v-if="!req.carwash_state">
            <label v-if="!req.carwash_state" for="mats">Are the mats in the car?</label>
            <div class="custom-control custom-checkbox custom-control-inline">
              <input
                type="checkbox"
                class="custom-control-input"
                id="mats"
                v-model="req.checked_mats"
              />
              <label class="custom-control-label" for="mats">Yes</label>
            </div>
          </div>
          <div v-if="!req.carwash_state">
            <label
              v-if="!req.carwash_state && req.license_plate =='Yes'"
              for="license_plate"
            >License Plate Bracket Installed?</label>
            <div
              v-if="req.license_plate == 'Yes' && !req.carwash_state"
              class="custom-control custom-checkbox custom-control-inline"
            >
              <input
                type="checkbox"
                class="custom-control-input"
                id="license_plate"
                v-model="req.license_plate_installed"
              />
              <label class="custom-control-label" for="license_plate">Yes</label>
            </div>
          </div>
          <hr v-if="!req.carwash_state" />
          <label v-if="!req.carwash_state" for="delivery_bay">Delivery Bay</label>
          <select
            v-if="!req.carwash_state"
            id="delivery_bay"
            class="form-control form-control-lg"
            v-model="req.delivery_bay"
          >
            <option v-for="(del, index) in delivery" :key="index">{{ del }}</option>
          </select>
          <v-btn
            v-if="req.carwash_state"
            @click="claimed(req.id, req.carwash_associate, req.status_carwash)"
            color="success"
          >Claim</v-btn>
          <v-btn outline color="error" v-if="!req.carwash_state" @click="unclaimed(req.id)">Unclaim</v-btn>
          <v-btn
            v-if="!req.carwash_state"
            @click="complete(req.id, req.checked_mats, req.license_plate_installed, req.delivery_bay)"
            color="success"
          >Complete</v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../main";
import router from "../router";

export default {
  name: "Carwash",
  data() {
    return {
      reqs: [],
      approvals: ["Claimed"],
      managers: [
        "Javier Martinez",
        "John Phan",
        "Zach Brown",
        "Chris Gatica",
        "Logan Greenwood",
        "Oscar Chestang"
      ],
      delivery: ["Bay 1", "Bay 2", "Bay 3", "Bay 4", "Delivery Bay Overflow"],
      mats: ["Yes", "No"],
      completionSales: "",
      salesManager: "",
      claimedStatus: "",
      carwashassociate: "",
      delivery_bay: ""
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("status_sales", "==", "Approved")
        .where("send_to_carwash", "==", true)
    };
  },
  methods: {
    claimed(id, associate, status) {
      db.collection("makeReady")
        .doc(id)
        .update({
          carwash_associate: associate,
          carwash_claimed_timestamp: new Date(),
          status_carwash: status,
          carwash_state: false
        });
    },
    unclaimed(id) {
      db.collection("makeReady")
        .doc(id)
        .update({
          carwash_associate: "",
          carwash_claimed_timestamp: "",
          status_carwash: "In progress...",
          carwash_state: true
        });
    },
    complete(id, checked_mats, license_plate_installed, delivery_bay) {
      db.collection("makeReady")
        .doc(id)
        .update({
          status_carwash: "Complete",
          carwash_complete_timestamp: new Date(),
          delivery_bay: delivery_bay,
          checked_mats: checked_mats,
          license_plate_installed: license_plate_installed,
          send_to_carwash: false
        });
    },
    delete_request(id) {
      let person = prompt("Please enter your sales manager PIN");
      if (
        person != null &&
        person != "" &&
        // Brian Barton
        (person == "6969" ||
          // Yvone Garcia
          person == "4598" ||
          // Miguel Valdez
          person == "8790" ||
          // Joshua Barazza
          person == "7030" ||
          // Don Wilkinson
          person == "5563" ||
          // Rolando Rocha
          person == "7206" ||
          // Barry Laird
          person == "2419")
      ) {
        db.collection("makeReady")
          .doc(id)
          .update({
            license_plate: "",
            parts_associate: "",
            status_parts: "",
            parts_completion_timestamp: "",
            parts_claimed_timestamp: "",
            send_to_parts: "",
            invoice_number: "",
            mats: "",
            mats_shelf: "",
            parts_state: "",
            finance_completion_timestamp: "",
            finance_claimed_timestamp: "",
            finance_associate: "",
            status_finance: "",
            send_to_finance: "",
            finance_state: "",
            carwash_claimed_timestamp: "",
            carwash_complete_timestamp: "",
            status_carwash: "",
            delivery_bay: "",
            checked_mats: "",
            carwash_associate: "",
            send_to_carwash: "",
            carwash_state: "",
            license_plate_installed: "",
            sales_completion_timestamp: "",
            status_sales: "'",
            sales_manager: "",
            password: "",
            delete_timestamp: new Date(),
            delete_status: true,
            delete_associate: person
          });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  margin-top: 20px;
  margin-bottom: 30px;
}

button {
  margin-top: 15px;
}

.container-fluid {
  width: 90%;
}

.heading {
  margin-bottom: 5rem;
  margin-top: 5rem;
}
</style>



